import React, { Component } from "react";
import firebase from "firebase";
// import firebaseui from "firebaseui";

import "./auth.css";
import { Field, reduxForm } from "redux-form";
import { connect } from "react-redux";
import { signIn, signOut } from "../actions";
import GoogleAuth from "./GoogleAuth";

class Auth extends Component {
  constructor(props) {
    super(props);
    this.state = {
      error: ""
    };
  }

  renderInput = ({ input, meta, label, type }) => {
    const confirmError = meta.error && meta.touched;
    const errorClassName = `field ${confirmError ? `error` : ``}`;

    return (
      <div className={errorClassName}>
        <label>
          {label}
          {`${confirmError ? "Requited" : ""}`}
        </label>
        <input {...input} type={type} />
      </div>
    );
  };
  onSubmit = formValue => {
    // console.log(formValue);
    firebase
      .auth()
      .signInWithEmailAndPassword(formValue.email, formValue.password)
      .then(result => {
        this.setState({ error: "Login Success" });
        console.log(result.user.uid);
        this.props.signIn(result.user.uid);
      })
      .catch(error => {
        // Handle Errors here.
        var errorCode = error.code;
        // console.log(errorCode);
        // var errorMessage = error.message;
        this.setState({ error: errorCode });
        // ...
        // console.log( "message:", errorMessage);
      });
  };
  renderError = error => {
    switch (error) {
      case "auth/argument-error":
        return "Please enter user and password!";
      case "auth/invalid-email":
        return "Please enter correct Email!";
      case "auth/user-not-found":
        return "Wrong user Or password";
      default:
        return "";
    }
  };
  render() {
    return (
      <div>
        <form
          onSubmit={this.props.handleSubmit(this.onSubmit)}
          className="ui form error"
        >
          <Field
            name="email"
            label="Email"
            component={this.renderInput}
          ></Field>
          <Field
            name="password"
            label="Password"
            component={this.renderInput}
          ></Field>
          <div>{this.renderError(this.state.error)}</div>
          <button type="submit" className="ui green approve right button">
            Login
          </button>
          <button className="ui green approve right button">Signup</button>
        </form>
        {/* <GoogleAuth /> */}
      </div>
    );
  }
}
const validate = formValue => {
  const error = {};
  if (!formValue.name) error.name = "Requite Value!";
  if (!formValue.price) error.price = "Requite Value!";

  return error;
};
const mapStateToProps = state => {
  return {
    isSignedIn: state.auth.isSignedIn
  };
};
const componentWrapup = connect(mapStateToProps, { signIn, signOut })(Auth);

export default reduxForm({
  form: "userForm",
  validate
})(componentWrapup);
